.info {
	background-color: $bg-gray;
	padding: 50px 0 120px 0;
	font-size: 14px;
	&__in {
		max-width: 760px; }
	&__body {
		margin: 37px 0 66px 0; }
	&__footer {
		padding: 30px 20px 20px 20px;
		background-color: white; }
	&__subtitle {
		color: $gray;
		margin-top: 38px; }
	&__title {
		margin: 42px 0 14px 0;
		font-size: 24px;
		@include regular;
		position: relative; } }

.info__list-item {
	margin: 13px 0;
	box-shadow: 0 1px 4px rgba(28, 28, 28, 0.15);
	border-radius: 3px;
	background-color: #ffffff;
	padding: 12px 16px 16px 20px; }
.info__list-title {
	@include bold;
	position: relative;
	cursor: pointer;
	.icon {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 0;
		@include tr(transform .3s ease); }
	&.is-active {
		.icon {
			transform: translateY(-50%) rotate(180deg); } }
