.info__list-content {
	color: $gray;
	max-height: 0;
	@include tr(max-height .3s ease-in);
	overflow: hidden;
	h5 {
		padding-top: 5px;
		font-size: 14px;
		@include regular; }
	p {
		padding: 2px 0; }
	ul {
		margin-bottom: 11px;
		list-style-type: disc;
		font-size: 14px;
		padding-left: 17px; } }
