html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arr {
  width: 4.83em;
  height: 1em;
  fill: initial;
}

.icon-arrd {
  width: 1em;
  height: 1em;
  fill: #bcbcbc;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/*=== import ===*/
@font-face {
  font-family: "Clear Sans Bold Italic";
  src: url("../fonts/ClearSans-Bolditalic.woff2") format("woff2"), url("../fonts/ClearSans-Bolditalic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Clear Sans Bold";
  src: url("../fonts/ClearSans-Bold.woff2") format("woff2"), url("../fonts/ClearSans-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Clear Sans Medium Italic";
  src: url("../fonts/ClearSans-Mediumitalic.woff2") format("woff2"), url("../fonts/ClearSans-Mediumitalic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Clear Sans Medium";
  src: url("../fonts/ClearSans-Medium.woff2") format("woff2"), url("../fonts/ClearSans-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Clear Sans Regular";
  src: url("../fonts/ClearSans-Regular.woff2") format("woff2"), url("../fonts/ClearSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Clear Sans Thin";
  src: url("../fonts/ClearSans-Thin.woff2") format("woff2"), url("../fonts/ClearSans-Thin.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Clear Sans Light";
  src: url("../fonts/ClearSans-Light.woff2") format("woff2"), url("../fonts/ClearSans-Light.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/*=== mixins ===*/
.title-secondary {
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  font-size: 18px;
  text-align: center;
  letter-spacing: 3.6px;
  text-transform: uppercase;
}

.icon-arr {
  font-size: 6px;
  fill: #000;
}

.icon-arrd {
  font-size: 24px;
  fill: #bcbcbc;
}

body {
  background: #fff;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  outline: none;
  border: 0;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  padding: 5px 15px;
  position: relative;
  min-width: 200px;
  color: #fff;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  background: #e86278;
  transition: background 0.25s;
}

.btn:hover {
  background: #e54c65;
}

.btn:active {
  top: 1px;
}

.btn_primary {
  background: #6dca14;
}

.btn_primary:hover {
  background: #60b312;
}

.btn_secondary {
  background: #fff;
  font-size: 12px;
  color: #000;
}

.btn_secondary:hover {
  background: #f2f2f2;
}

.form__field, .form__textarea {
  width: 100%;
  outline: 0;
  display: block;
  border: 0;
}

.inner {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.out {
  flex-grow: 1;
}

/* == Header start == */
.header {
  position: relative;
}

.header__top {
  height: 38px;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.header__top:after {
  content: '';
  display: block;
  position: absolute;
  background-color: #e5e5e5;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1px;
}

.header__top-left {
  display: flex;
  align-items: center;
}

.header__top-rigth {
  display: flex;
  align-items: center;
}

.header__bottom {
  height: 90px;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.header__bottom-left, .header__bottom-right {
  display: flex;
  align-items: center;
}

.header__bottom-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.header__btn {
  min-width: 172px;
  font-size: 12px;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  letter-spacing: 0.6px;
  margin: 0 0 0 56px;
}

.header__messengers {
  margin: 0 0 0 10px;
}

.header__logo {
  background: url(../img/logo-red.png) no-repeat center;
  width: 200px;
  height: 50px;
  display: block;
  transition: opacity 0.25s;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.header__logo:hover {
  opacity: 0.7;
}

.nav {
  position: relative;
  display: flex;
  align-items: center;
}

.nav__link {
  color: #000;
  font-size: 10px;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  line-height: 1.4;
  letter-spacing: 0.25px;
  text-transform: uppercase;
  transition: color 0.25s;
  padding: 10px 15px;
}

.nav__link.is-active, .nav__link:hover {
  color: #bdbdbd;
}

.nav__link:first-of-type {
  padding-left: 0;
}

.nav__link:last-of-type {
  padding-right: 0;
}

.form-links {
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  color: #000;
  font-size: 12px;
  letter-spacing: 0.6px;
  display: inline-flex;
  align-items: center;
}

.form-links__item {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  line-height: 1.4;
  padding: 5px 2px;
  transition: color 0.25s;
}

.form-links__item:first-of-type {
  padding-left: 0;
}

.form-links__item:last-of-type {
  padding-right: 0;
}

.form-links__item:hover {
  color: #8c8c8c;
}

.tselect {
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  line-height: 1.2;
  color: #000000;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 0.9px;
  height: 100%;
  display: flex;
  align-items: center;
}

.tselect.is-active .tselect__list {
  display: block;
}

.tselect__current {
  padding: 0 20px 0 0;
  position: relative;
}

.tselect__current-link {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  transition: opacity 0.25s;
}

.tselect__current-link:hover {
  opacity: 0.6;
}

.tselect__arrow {
  width: 15px;
  height: 10px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;
}

.tselect__arrow:before {
  content: '';
  display: block;
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 4.5px 0 4.5px;
  border-color: #000 transparent transparent transparent;
}

.tselect__list {
  position: absolute;
  top: 100%;
  left: 0;
  background: #fff;
  z-index: 10;
  display: none;
}

.tselect__item {
  cursor: pointer;
  transition: opacity 0.25s;
}

.tselect__item:hover {
  opacity: 0.6;
}

.messengers {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  display: inline-flex;
  align-items: center;
}

.messengers__item {
  margin: 0 15px 0 0;
}

.messengers__item:last-of-type {
  margin: 0;
}

.messengers__links {
  display: inline-block;
  cursor: pointer;
  transition: opacity 0.25s;
}

.messengers__links:hover {
  opacity: 0.6;
}

.messengers__whatsapp {
  width: 25px;
  height: 25px;
  background: url(data:image/png;base64,) no-repeat center;
}

.messengers__viber {
  width: 18px;
  height: 18px;
  background: url(data:image/png;base64,) no-repeat center;
}

.messengers__telegram {
  width: 25px;
  height: 25px;
  background: url(data:image/png;base64,) no-repeat center;
}

.pselect {
  color: #000;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 0.5px;
  margin: 0 20px 0 0;
  position: relative;
}

.pselect__current {
  position: relative;
  padding: 0 20px 0 0;
  cursor: pointer;
}

.pselect__current:after {
  content: '';
  display: block;
  position: absolute;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 4.5px 0 4.5px;
  border-color: #000 transparent transparent transparent;
  top: 0;
  bottom: 0;
  right: 5px;
  margin: auto;
}

.pselect__list {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 10;
}

.pselect__item {
  text-transform: uppercase;
  white-space: nowrap;
  cursor: pointer;
}

.cselect {
  position: relative;
  margin: 0 20px 0 0;
}

.cselect.is-active .cselect__list {
  display: block;
}

.cselect__current {
  position: relative;
  padding: 0 20px 0 0;
  cursor: pointer;
}

.cselect__current:after {
  content: '';
  display: block;
  position: absolute;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 4px 4.5px 0 4.5px;
  border-color: #000 transparent transparent transparent;
  top: 0;
  bottom: 0;
  right: 5px;
  margin: auto;
}

.cselect__list {
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  padding: 5px 0;
  z-index: 10;
}

.cselect__item {
  padding: 5px 0;
  cursor: pointer;
}

.cselect img {
  display: block;
}

/* == Header end == */
/* == Footer start == */
.footer {
  position: relative;
  flex-shrink: 0;
  flex-grow: 0;
}

.footer__top-in {
  position: relative;
  padding: 25px 15px 10px;
  display: flex;
}

.footer__top-in:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 1px;
  background: #e5e5e5;
}

.footer__top-left {
  width: calc(100% - 300px);
  padding: 5px 0 0;
}

.footer__top-right {
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer__bottom {
  background-color: #e8e8e8;
}

.footer__bottom-in {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__bottom-left {
  display: flex;
}

.footer__messengers {
  margin: 15px 0 0;
}

.footer__logo {
  width: 120px;
  height: 30px;
  background: url(../img/logo-black.png) no-repeat center;
  display: inline-block;
  transition: opacity 0.25s;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.footer__logo:hover {
  opacity: 0.7;
}

.fnav {
  column-count: 3;
}

.fnav__item {
  display: flex;
}

.fnav__link {
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  color: #818181;
  font-size: 16px;
  line-height: 1.4;
  position: relative;
  padding: 5px 20px;
  display: inline-block;
  transition: color 0.25s;
}

.fnav__link:hover {
  color: #0e0e0e;
}

.fnav__link:before {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #000;
  position: absolute;
  left: 10px;
  top: 0;
  bottom: 0;
  margin: auto;
}

.tel {
  color: #000;
  font-size: 30px;
  line-height: 1.2;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  transition: color 0.25s;
  cursor: pointer;
}

.tel:hover {
  color: gray;
}

.copyright {
  font-size: 14px;
  line-height: 1.1;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  color: #000;
  margin: 0 0 0 30px;
}

.social {
  display: flex;
}

.social__item {
  margin: 0 15px 0 0;
  display: flex;
  align-items: center;
}

.social__item:last-of-type {
  margin: 0;
}

.social__link {
  display: block;
  transition: opacity 0.25s;
}

.social__link:hover {
  opacity: 0.6;
}

.social__fb {
  width: 8px;
  height: 17px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAARCAYAAADg1u3YAAAAc0lEQVQoU9WQQQ2AMBAEp0pAAg4ICrCABRwgAQeAE3BSCyiALOklLUlJv9yzN93Z1pHOBPRAY8cu2g/A8rpADOxAWwqMwCw4l9ABSnwAFbsAdaiDYgW8AVrm5lTCF3CYQglSVCFqCwpfVNL88T8kr/g/cANTiCGeEtunhQAAAABJRU5ErkJggg==) no-repeat center;
}

.social__tw {
  width: 18px;
  height: 15px;
  background: url(data:image/png;base64,) no-repeat center;
}

.social__vk {
  width: 26px;
  height: 16px;
  background: url(data:image/png;base64,) no-repeat center;
}

.social__insta {
  width: 20px;
  height: 20px;
  background: url(data:image/png;base64,) no-repeat center;
}

/* == Footer end == */
.form__row {
  margin-bottom: 9px;
}

.form__field, .form__textarea {
  border-radius: 3px;
  padding: 9px 13px;
  color: #939393;
  background-color: #f3f3f3;
  font-size: 14px;
}

.form__textarea {
  min-height: 110px;
  resize: none;
  letter-spacing: 0.35px;
}

.form__footer {
  text-align: center;
  margin-top: 13px;
}

.form__btn {
  font-size: 12px;
}

.main {
  text-align: center;
  color: #fff;
  height: 640px;
  background: url(../img/main-section-bg.jpg) no-repeat center;
  background-size: cover;
}

.main__title {
  text-shadow: 0 1px 0 #000000;
  font-size: 42px;
  line-height: 1.2;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  letter-spacing: 1.05px;
}

.main__subtitle {
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.35px;
  margin: 16px 0 0;
}

.main__buttons-wrap {
  margin: 94px 0 0;
}

.main__buttons-wrap .btn + .btn {
  margin: 0 0 0 20px;
}

.main__content {
  display: inline-block;
  padding: 90px 0 50px;
}

.main__btn {
  box-shadow: 0 3px 13px rgba(28, 28, 28, 0.5);
}

.info {
  background-color: #f3f3f3;
  padding: 50px 0 120px 0;
  font-size: 14px;
}

.info__in {
  max-width: 760px;
}

.info__body {
  margin: 37px 0 66px 0;
}

.info__footer {
  padding: 30px 20px 20px 20px;
  background-color: white;
}

.info__subtitle {
  color: #818181;
  margin-top: 38px;
}

.info__title {
  margin: 42px 0 14px 0;
  font-size: 24px;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
  position: relative;
}

.info__list-item {
  margin: 13px 0;
  box-shadow: 0 1px 4px rgba(28, 28, 28, 0.15);
  border-radius: 3px;
  background-color: #ffffff;
  padding: 12px 16px 16px 20px;
}

.info__list-title {
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  position: relative;
  cursor: pointer;
}

.info__list-title .icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  transition: transform 0.3s ease;
}

.info__list-title.is-active .icon {
  transform: translateY(-50%) rotate(180deg);
}

.info__list-content {
  color: #818181;
  max-height: 0;
  transition: max-height 0.3s ease-in;
  overflow: hidden;
}

.info__list-content h5 {
  padding-top: 5px;
  font-size: 14px;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
}

.info__list-content p {
  padding: 2px 0;
}

.info__list-content ul {
  margin-bottom: 11px;
  list-style-type: disc;
  font-size: 14px;
  padding-left: 17px;
}
