.inner {
	max-width: 1140px;
	margin: 0 auto;
	padding: 0 15px; }
.out {
	flex-grow: 1; }
/* == Header start == */
.header {
	position: relative;
	&__top {
		height: 38px;
		display: flex;
		justify-content: space-between;
		position: relative;
		&:after {
			content: '';
			display: block;
			position: absolute;
			background-color: #e5e5e5;
			left: 0;
			right: 0;
			bottom: 0;
			height: 1px; } }
	&__top-left {
		display: flex;
		align-items: center; }
	&__top-rigth {
		display: flex;
		align-items: center; }
	&__bottom {
		height: 90px;
		display: flex;
		justify-content: space-between;
		position: relative; }
	&__bottom-left,
	&__bottom-right {
		display: flex;
		align-items: center; }
	&__bottom-center {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); }
	&__btn {
		min-width: 172px;
		font-size: 12px;
		@include bold;
		letter-spacing: 0.6px;
		margin: 0 0 0 56px; }
	&__messengers {
		margin: 0 0 0 10px; }
	&__logo {
		background: url(../img/logo-red.png) no-repeat center;
		width: 200px;
		height: 50px;
		display: block;
		transition: opacity 0.25s;
		@include hide-text;
		&:hover {
			opacity: 0.7; } } }
.nav {
	position: relative;
	display: flex;
	align-items: center;
	&__link {
		color: $black;
		font-size: 10px;
		@include bold;
		line-height: 1.4;
		letter-spacing: 0.25px;
		text-transform: uppercase;
		transition: color 0.25s;
		padding: 10px 15px;
		&.is-active,
		&:hover {
			color: lighten($black, 74%); }
		&:first-of-type {
			padding-left: 0; }
		&:last-of-type {
			padding-right: 0; } } }
.form-links {
	@include bold;
	color: $black;
	font-size: 12px;
	letter-spacing: 0.6px;
	display: inline-flex;
	align-items: center;
	&__item {
		color: inherit;
		font-family: inherit;
		font-size: inherit;
		line-height: 1.4;
		padding: 5px 2px;
		transition: color 0.25s;
		&:first-of-type {
			padding-left: 0; }
		&:last-of-type {
			padding-right: 0; }
		&:hover {
			color: lighten($black, 55%); } } }
.tselect {
	@include bold;
	line-height: 1.2;
	color: #000000;
	font-size: 18px;
	text-transform: uppercase;
	letter-spacing: 0.9px;
	height: 100%;
	display: flex;
	align-items: center;
	&.is-active {
		.tselect__list {
			display: block; } }
	&__current {
		padding: 0 20px 0 0;
		position: relative; }
	&__current-link {
		font-family: inherit;
		font-size: inherit;
		line-height: inherit;
		color: inherit;
		transition: opacity 0.25s;
		&:hover {
			opacity: 0.6; } }
	&__arrow {
		width: 15px;
		height: 10px;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		cursor: pointer;
		&:before {
			content: '';
			display: block;
			position: absolute;
			@include vcenter;
			@include arr(9, 4, $black, b); } }
	&__list {
		position: absolute;
		top: 100%;
		left: 0;
		background: $white;
		z-index: 10;
		display: none; }
	&__item {
		cursor: pointer;
		transition: opacity 0.25s;
		&:hover {
			opacity: 0.6; } } }
.messengers {
	@include hide-text;
	display: inline-flex;
	align-items: center;
	&__item {
		margin: 0 15px 0 0;
		&:last-of-type {
			margin: 0; } }
	&__links {
		display: inline-block;
		cursor: pointer;
		transition: opacity 0.25s;
		&:hover {
			opacity: 0.6; } }
	&__whatsapp {
		width: 25px;
		height: 25px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__viber {
		width: 18px;
		height: 18px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__telegram {
		width: 25px;
		height: 25px;
		background: url(data:image/png;base64,) no-repeat center; } }
.pselect {
	color: $black;
	@include regular;
	font-size: 20px;
	line-height: 1.2;
	letter-spacing: 0.5px;
	margin: 0 20px 0 0;
	position: relative;
	&__current {
		position: relative;
		padding: 0 20px 0 0;
		cursor: pointer;
		&:after {
			content: '';
			display: block;
			position: absolute;
			@include arr(9, 4, $black, b);
			top: 0;
			bottom: 0;
			right: 5px;
			margin: auto; } }
	&__list {
		display: none;
		position: absolute;
		top: 100%;
		left: 0;
		z-index: 10; }
	&__item {
		text-transform: uppercase;
		white-space: nowrap;
		cursor: pointer; } }
.cselect {
	position: relative;
	margin: 0 20px 0 0;
	&.is-active {
		.cselect__list {
			display: block; } }
	&__current {
		position: relative;
		padding: 0 20px 0 0;
		cursor: pointer;
		&:after {
			content: '';
			display: block;
			position: absolute;
			@include arr(9, 4, $black, b);
			top: 0;
			bottom: 0;
			right: 5px;
			margin: auto; } }
	&__list {
		position: absolute;
		top: 100%;
		left: 0;
		display: none;
		padding: 5px 0;
		z-index: 10; }
	&__item {
		padding: 5px 0;
		cursor: pointer; }
	img {
		display: block; } }
/* == Header end == */

/* == Footer start == */
.footer {
	position: relative;
	flex-shrink: 0;
	flex-grow: 0;
	&__top-in {
		position: relative;
		padding: 25px 15px 10px;
		display: flex;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			height: 1px;
			background: #e5e5e5; } }
	&__top-left {
		width: calc(100% - 300px);
		padding: 5px 0 0; }
	&__top-right {
		width: 300px;
		display: flex;
		flex-direction: column;
		align-items: flex-end; }
	&__bottom {
		background-color: #e8e8e8; }
	&__bottom-in {
		height: 80px;
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__bottom-left {
		display: flex; }
	&__messengers {
		margin: 15px 0 0; }
	&__logo {
		width: 120px;
		height: 30px;
		background: url(../img/logo-black.png) no-repeat center;
		display: inline-block;
		transition: opacity 0.25s;
		@include hide-text;
		&:hover {
			opacity: 0.7; } } }
.fnav {
	column-count: 3;
	&__item {
		display: flex; }
	&__link {
		@include regular;
		color: #818181;
		font-size: 16px;
		line-height: 1.4;
		position: relative;
		padding: 5px 20px;
		display: inline-block;
		transition: color 0.25s;
		&:hover {
			color: darken(#818181, 45%); }
		&:before {
			content: '';
			display: block;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background: $black;
			position: absolute;
			left: 10px;
			top: 0;
			bottom: 0;
			margin: auto; } } }
.tel {
	color: $black;
	font-size: 30px;
	line-height: 1.2;
	@include bold;
	transition: color 0.25s;
	cursor: pointer;
	&:hover {
		color: lighten($black, 50%); } }
.copyright {
	font-size: 14px;
	line-height: 1.1;
	@include regular;
	color: $black;
	margin: 0 0 0 30px; }
.social {
	display: flex;
	&__item {
		margin: 0 15px 0 0;
		display: flex;
		align-items: center;
		&:last-of-type {
			margin: 0; } }
	&__link {
		display: block;
		transition: opacity 0.25s;
		&:hover {
			opacity: 0.6; } }
	&__fb {
		width: 8px;
		height: 17px;
		background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAARCAYAAADg1u3YAAAAc0lEQVQoU9WQQQ2AMBAEp0pAAg4ICrCABRwgAQeAE3BSCyiALOklLUlJv9yzN93Z1pHOBPRAY8cu2g/A8rpADOxAWwqMwCw4l9ABSnwAFbsAdaiDYgW8AVrm5lTCF3CYQglSVCFqCwpfVNL88T8kr/g/cANTiCGeEtunhQAAAABJRU5ErkJggg==) no-repeat center; }
	&__tw {
		width: 18px;
		height: 15px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__vk {
		width: 26px;
		height: 16px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__insta {
		width: 20px;
		height: 20px;
		background: url(data:image/png;base64,) no-repeat center; } }
/* == Footer end == */
